import React, {useEffect, useState} from 'react';
import './index.scss';
import {mapPoint} from '../utils/pointMaps'
import {useParams} from "react-router-dom";
import ScrollToTop from "../utils/ScrollToTop";
import useWindowSize from "../utils/useWindowSize";


// Интерфейс для точек
interface Point {
    id: number;
    x: number;
    y: number;
}

// Интерфейс для линий
interface Line {
    startX: number;
    startY: number;
    endX: number;
    endY: number;
}

// Компонент точки
function PointComponent({
                            id,
                            x,
                            y,
                            onStartLine,
                        }: {
    id: number;
    x: number;
    y: number;
    onStartLine: () => void;
}) {
    const handleMouseDown = () => {
        onStartLine();
    };

    const handleTouchStart = () => {
        onStartLine();
    };

    return (
        <div
            onMouseDown={handleMouseDown}
            onTouchStart={handleTouchStart} // Обработчик для сенсорного ввода
            className={'point'}
            style={{
                zIndex: '2',
                position: 'absolute',
                left: x + 'px',
                top: y + 'px',

            }}
        >
            <span className='id'>{id}</span>
        </div>
    );
}

// Компонент линии
function LineComponent({startX, startY, endX, endY}: Line) {
    return (
        <svg
            width="100%"
            height="100%"
            style={{position: 'absolute', top: 0, left: 0, zIndex: 1}}
        >
            <line x1={startX} y1={startY} x2={endX} y2={endY} stroke="#FFF" strokeWidth="3"/>
        </svg>
    );
}

// Компонент области рисования
function DrawingArea({
                         points,
                         pointClassProps,
                         setLevelEnd
                     }: { points: Point[], pointClassProps: string, setLevelEnd: React.Dispatch<React.SetStateAction<boolean>> }) {
    const [lines, setLines] = useState<Line[]>([]);
    const [isDrawing, setIsDrawing] = useState(false);
    const [startPointId, setStartPointId] = useState<number | null>(null);
    const [currentX, setCurrentX] = useState<number | null>(null);
    const [currentY, setCurrentY] = useState<number | null>(null);
    const [indexPointId, setIndexPointId] = useState<number>(0);
    const handleStartLine = (id: number) => {
        setIsDrawing(true);
        setStartPointId(id);
    };
    useEffect(() => {
        setLines([])
    }, [points])
    const gameWrap = document.getElementById('gameWrap');

    const handleMouseMove = (event: React.MouseEvent) => {
        if (isDrawing && startPointId !== null && gameWrap ) {
            setCurrentX(event.clientX - gameWrap.offsetLeft);
            setCurrentY(event.clientY - gameWrap.offsetTop);
        }
    };

    const handleTouchMove = (event: React.TouchEvent) => {
        if (isDrawing && startPointId !== null && gameWrap ) {
            const touch = event.touches[0];
            setCurrentX(touch.clientX - gameWrap.offsetLeft);
            setCurrentY(touch.clientY - gameWrap.offsetTop);
        }
    };


    const handleMouseUp = () => {
        if (isDrawing && startPointId !== null && currentX !== null && currentY !== null) {
            const xRange = Array.from({length: 31}, (_, i) => points[startPointId].x - 10 + i);
            const yRange = Array.from({length: 31}, (_, i) => points[startPointId].y - 10 + i);

            if (xRange.includes(Math.ceil(currentX)) && yRange.includes(Math.ceil(currentY))) {
                const newLines = [...lines];
                newLines.push({
                    startX: points[startPointId - 1].x + 6,
                    startY: points[startPointId - 1].y + 6,
                    endX: points[startPointId].x + 6,
                    endY: points[startPointId].y + 6,
                });
                setLines(newLines);
                setIndexPointId(indexPointId => indexPointId + 1)
                if (!points[startPointId + 1]) {
                    setLevelEnd(true)
                }
            }
            setIsDrawing(false);
            setStartPointId(null);
            setCurrentX(null);
            setCurrentY(null);
        }
    };


    return (
        <div
            id="gameWrap"
            className={pointClassProps}
            style={{position: 'relative'}}
            onMouseMove={handleMouseMove}
            onMouseUp={handleMouseUp}
            onTouchMove={handleTouchMove}
            onTouchEnd={handleMouseUp}
        >
            {lines.map((line, index) => (
                <LineComponent
                    key={index}
                    startX={line.startX}
                    startY={line.startY}
                    endX={line.endX}
                    endY={line.endY}
                />
            ))}
            {points.map((point) => (
                <PointComponent
                    key={point.id}
                    id={point.id}
                    x={point.x}
                    y={point.y}
                    onStartLine={() => handleStartLine(point.id)}
                />
            ))}
            {isDrawing && startPointId !== null && currentX !== null && currentY !== null && (
                <LineComponent
                    startX={points[startPointId - 1].x + 6}
                    startY={points[startPointId - 1].y + 6}
                    endX={currentX}
                    endY={currentY}
                />
            )}
        </div>
    );
}

interface propsGame {
    initialPoints: Point[],
    setModalOpen: React.Dispatch<React.SetStateAction<boolean>>,
    levelEnd: boolean,
    Img: string;
    setLevelEnd: React.Dispatch<React.SetStateAction<boolean>>
}

// Основной компонент
function Game(props: propsGame) {
    const {Img, initialPoints, setModalOpen, levelEnd, setLevelEnd} = props
    const {width} = useWindowSize()
    useEffect(() => {
        setTimeout(() => {
            if (levelEnd)
                setModalOpen(true)
        }, 300)
    }, [levelEnd])
    useEffect(() => {
        document.body.style.overflow = "hidden";
        if(width===320){
            for (let i = 0; i < initialPoints.length; i++) {
                initialPoints[i].x -= 45;
            }
        }
    }, []);


    return (
        <div className="GameContainer">
            <DrawingArea points={initialPoints} pointClassProps={levelEnd ? 'wrapPontEnd' : ''}
                         setLevelEnd={setLevelEnd}/>
            <img className={levelEnd ? 'imgOpen' : 'imgClose'} src={Img}/>
        </div>
    );
}

export default Game;
