import React, {useEffect} from 'react';
import './index.scss'
import {Link} from "react-router-dom";
import {buttonEnd} from "../utils/textEndlevel";

interface modalProps{
    openModal:boolean,
    textBodyEnd:string,
    buttonEnd:string,
    id:string | undefined,
    setLevelEnd: React.Dispatch<React.SetStateAction<boolean>>,
    finalPage:boolean,

}
const Modal = (modalProps: modalProps) => {
    const {openModal, textBodyEnd, buttonEnd, id, setLevelEnd, finalPage} = modalProps
    // useEffect(() => {
    //     if (openModal)
    //         document.body.style.overflow = "hidden";
    //     else document.body.style.overflow = "inherit";
    //
    // }, [openModal]);


    return (
        <div className={`modalBg  ${openModal ? 'modalShow' : ""}`}>
            <div className={'positionWrap'}>
           <div className={'modalWrap'}>
               <div className={'modalText'}>
                   {textBodyEnd}
               </div>
            <Link to={!finalPage ? '/final' :`/game/${Number(id) + 1}`}>
            <button className={'buttonNext'} onClick={()=>setLevelEnd(false)}>
                {buttonEnd}
            </button>
            </Link>
           </div>
            </div>
        </div>
    );
};

export default Modal;
